import React from "react";
import PropTypes from "prop-types";
import styles from "./SortComponent.module.scss";

export function SortComponent({ sortType, setSortType }) {
  return (
    <div className={styles.sortOptions}>
      <select value={sortType} onChange={(e) => setSortType(e.target.value)}>
        <option value="default"> </option>
        <option value="alphabetAsc"> (-)</option>
        <option value="alphabetDesc"> (-)</option>
        <option value="priceAsc"> ( )</option>
        <option value="priceDesc"> ( )</option>
      </select>
    </div>
  );
}

export function SortLotsComponent({ sortType, setSortType }) {
  return (
    <div className={styles.sortOptions}>
      <select name="sortType" value={sortType} onChange={(e) => setSortType(e.target.value)}>
        <option value="firstNew"> </option>
        <option value="expirationDate">  </option>
        <option value="lowestBid">  </option>
        <option value="highestBid">  </option>
      </select>
    </div>
  );
}

export function SortDonateComponent({ sortType, setSortType }) {
  return (
    <div className={styles.sortOptions}>
      <select name="sortType" value={sortType} onChange={(e) => setSortType(e.target.value)}>
        <option value="oldestFirst"> </option>
        <option value="newestFirst"> </option>
        <option value="endDate">  </option>
        <option value="percentageFound">   </option>
      </select>
    </div>
  );
}


const SortComponents = {
  SortComponent,
  SortLotsComponent,
  SortDonateComponent,
};

export default SortComponents;

SortComponent.propTypes = {
  sortType: PropTypes.string.isRequired,
  setSortType: PropTypes.func.isRequired,
};

SortLotsComponent.propTypes = {
  sortType: PropTypes.string.isRequired,
  setSortType: PropTypes.func.isRequired,
};

SortDonateComponent.propTypes = {
  sortType: PropTypes.string.isRequired,
  setSortType: PropTypes.func.isRequired,
};
