import React from "react";
import { useSelector } from "react-redux";
import FavoritesItem from "./FavoritesItem";
import styles from "./Favorites.module.scss";


function Favorites() {
  const favoritesItems = useSelector((state) => state.favorites.items);
  const favoritesItemsGoods = favoritesItems.filter((product) => product.category === "");
  const favoritesItemsNotGoods = favoritesItems.filter((product) => product.category !== "");
  // const isFavoriteEmpty = favoritesItems.length === 0;

  return (
    <div className={styles.cardsSectionWrapper}>
      <h1 className={styles.cardsSectionHeadline}> </h1>
      <p className={styles.cardsSectionText}>  </p>

      {favoritesItemsGoods.length === 0 ? (
        <p className={styles.favoriteEmpty}>     </p>
      ) : (
        <div className={styles.cardsListWrapper}>
          {favoritesItems.map((item) => {
            if (item.category === "") {
              return (
                <FavoritesItem
                  key={item.itemNo}
                  item={item}
                />
              );
            }
            return null;
          })}
        </div>
      )}
      
      <h2 className={styles.cardsSectionHeadline}> </h2>
      <p className={styles.cardsSectionText}>  </p>
      {favoritesItemsNotGoods.length === 0 ? (
        <p className={styles.favoriteEmpty}>       </p>
      ) : (
        <div className={styles.cardsListDonatWrapper}>
          {favoritesItems.map((item) => {
            if (item.category === " " || item.category === "") {
              return (
                <FavoritesItem
                  key={item.itemNo}
                  item={item}
                />
              );
            }
            return null;
          })}
        </div>
      )}
    </div>
  );
}

export default Favorites;

